/* src/components/Founders.css */

/* Section Styles */
.founders-section {
  padding: 100px 24px;
  background: linear-gradient(135deg, #0f172a 0%, #1e293b 50%, #334155 100%);
  position: relative;
  overflow: hidden;
}

.founders-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: radial-gradient(
      circle at 20% 50%,
      rgba(59, 130, 246, 0.1) 0%,
      transparent 50%
    ),
    radial-gradient(
      circle at 80% 80%,
      rgba(139, 92, 246, 0.1) 0%,
      transparent 50%
    );
  pointer-events: none;
}

.founders-container-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

/* Header Styles */
.founders-header {
  text-align: center;
  margin-bottom: 70px;
  animation: fadeInDown 0.8s ease-out;
}

.founders-title {
  font-size: 3rem;
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 16px;
  letter-spacing: -0.5px;
  text-shadow: 0 4px 12px rgba(0, 0, 0, 0.3);
}

.founders-underline {
  width: 100px;
  height: 5px;
  background: linear-gradient(to right, #3b82f6, #8b5cf6, #ec4899);
  margin: 0 auto 24px;
  border-radius: 9999px;
  box-shadow: 0 0 20px rgba(59, 130, 246, 0.5);
}

.founders-subtitle {
  font-size: 1.25rem;
  color: rgba(255, 255, 255, 0.8);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.7;
}

/* Founders Grid */
.founders-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 48px;
  margin-bottom: 60px;
}

/* Founder Card */
.founder-card {
  position: relative;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 24px;
  padding: 48px 32px;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  overflow: hidden;
  animation: fadeInUp 0.8s ease-out backwards;
}

.founder-card:nth-child(1) {
  animation-delay: 0.1s;
}

.founder-card:nth-child(2) {
  animation-delay: 0.2s;
}

.founder-card:hover {
  transform: translateY(-12px);
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(59, 130, 246, 0.3);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
}

/* Card Background Decoration */
.card-background {
  position: absolute;
  top: -50%;
  right: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(59, 130, 246, 0.1), transparent 70%);
  opacity: 0;
  transition: opacity 0.5s ease;
}

.founder-card:hover .card-background {
  opacity: 1;
}

/* Card Shine Effect */
.card-shine {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
  transition: left 0.6s ease;
}

.founder-card:hover .card-shine {
  left: 100%;
}

/* Image Wrapper */
.founder-image-wrapper {
  position: relative;
  width: 160px;
  height: 160px;
  margin: 0 auto 32px;
}

.image-ring {
  position: absolute;
  inset: -8px;
  border-radius: 50%;
  background: linear-gradient(135deg, #3b82f6, #8b5cf6, #ec4899);
  animation: rotate 4s linear infinite;
  opacity: 0;
  transition: opacity 0.4s ease;
}

.founder-card:hover .image-ring {
  opacity: 1;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.founder-img {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid rgba(255, 255, 255, 0.1);
  transition: all 0.4s ease;
  z-index: 2;
}

.founder-card:hover .founder-img {
  transform: scale(1.05);
  border-color: rgba(59, 130, 246, 0.5);
}

/* Image Overlay */
.image-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(
    135deg,
    rgba(59, 130, 246, 0.8),
    rgba(139, 92, 246, 0.8)
  );
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.4s ease;
  z-index: 3;
}

.founder-card:hover .image-overlay {
  opacity: 1;
}

.overlay-icon {
  width: 50px;
  height: 50px;
  color: white;
  animation: scaleIn 0.4s ease;
}

@keyframes scaleIn {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

/* Content */
.founder-content {
  text-align: center;
  position: relative;
  z-index: 2;
}

.founder-name {
  font-size: 1.75rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 8px;
  letter-spacing: -0.3px;
}

.founder-role {
  font-size: 1rem;
  font-weight: 600;
  color: #3b82f6;
  margin-bottom: 16px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.founder-description {
  color: rgba(255, 255, 255, 0.7);
  line-height: 1.7;
  margin-bottom: 24px;
  font-size: 0.95rem;
}

/* Specialties */
.founder-specialties {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 28px;
}

.specialty-tag {
  padding: 6px 16px;
  background: rgba(59, 130, 246, 0.15);
  border: 1px solid rgba(59, 130, 246, 0.3);
  border-radius: 20px;
  color: #60a5fa;
  font-size: 0.85rem;
  font-weight: 600;
  transition: all 0.3s ease;
}

.founder-card:hover .specialty-tag {
  background: rgba(59, 130, 246, 0.25);
  border-color: rgba(59, 130, 246, 0.5);
  color: #93c5fd;
  transform: translateY(-2px);
}

/* Social Links */
.founder-social {
  display: flex;
  justify-content: center;
  gap: 16px;
}

.social-link {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  transition: all 0.3s ease;
  cursor: pointer;
}

.social-link svg {
  width: 20px;
  height: 20px;
  color: rgba(255, 255, 255, 0.6);
  transition: color 0.3s ease;
}

.social-link:hover {
  transform: translateY(-4px);
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.2);
}

.social-link.linkedin:hover {
  background: rgba(10, 102, 194, 0.2);
  border-color: #0a66c2;
}

.social-link.linkedin:hover svg {
  color: #0a66c2;
}

.social-link.github:hover {
  background: rgba(51, 51, 51, 0.2);
  border-color: #333;
}

.social-link.github:hover svg {
  color: #ffffff;
}

.social-link.email:hover {
  background: rgba(234, 88, 12, 0.2);
  border-color: #ea580c;
}

.social-link.email:hover svg {
  color: #ea580c;
}

/* Footer */
.founders-footer {
  text-align: center;
  padding: 32px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  animation: fadeInUp 1s ease-out 0.4s backwards;
}

.footer-text {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.125rem;
  line-height: 1.7;
}

/* Animations */
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design */
@media (min-width: 768px) {
  .founders-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 767px) {
  .founders-section {
    padding: 70px 16px;
  }

  .founders-title {
    font-size: 2rem;
  }

  .founders-subtitle {
    font-size: 1rem;
  }

  .founders-header {
    margin-bottom: 50px;
  }

  .founders-grid {
    gap: 32px;
    margin-bottom: 48px;
  }

  .founder-card {
    padding: 40px 24px;
  }

  .founder-image-wrapper {
    width: 140px;
    height: 140px;
  }

  .founder-name {
    font-size: 1.5rem;
  }

  .founder-role {
    font-size: 0.9rem;
  }

  .founder-description {
    font-size: 0.9rem;
  }

  .specialty-tag {
    font-size: 0.8rem;
    padding: 5px 12px;
  }

  .social-link {
    width: 36px;
    height: 36px;
  }

  .social-link svg {
    width: 18px;
    height: 18px;
  }

  .footer-text {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .founders-title {
    font-size: 1.75rem;
  }

  .founders-grid {
    grid-template-columns: 1fr;
  }

  .founder-card {
    padding: 32px 20px;
  }

  .founder-image-wrapper {
    width: 120px;
    height: 120px;
  }

  .founder-name {
    font-size: 1.35rem;
  }

  .founders-footer {
    padding: 24px 16px;
  }

  .footer-text {
    font-size: 0.95rem;
  }
}
